#app {
  height: 100%;
}

html,
body {
  position: relative;
  height: auto;
}

ul,
li {
  list-style: none;
}

body {
  background: #fff;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  top: 0;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
  border-top: 2px solid #394177;
}

.start {
  transform: scale(0);

}

.end {
  transform: scale(1);
  transition: all 0.5s;
}

/*헤더 네비*/
.nav {
  width: 1500px;
  height: 118px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
}

.nav-link>img {
  width: 106px;
  height: 37px;
  cursor: pointer;
}

.nav-link>.nav-item:nth-of-type(1) {
  margin: 0 0 0 10px;
}

.nav-link>.nav-item {
  font-size: 16px;
  color: #535353;
  font-weight: bold;
  border-bottom: 2px solid #fff;
  margin: 0 7px;
  cursor: pointer;
}

.nav-link>.nav-item:hover {
  color: #394177;
  border-bottom: 2px solid #394177;
}

.menuicon {
  width: 26px;
  margin: 0 0 5px 10px;
  cursor: pointer;
}

.iconbox {
  margin: 0 0 0 90px;
  display: flex;
}

.iconbox>.nav-link {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 -7px;
}

.iconbox>.nav-link>span {
  font-size: 11px;
  color: #000;
  margin: 5px 0;
}

.iconbox>.nav-link>svg {
  color: #535353;
  cursor: pointer;
  width: 20px;
}

.iconbox>.nav-link>svg:hover {
  color: #394177;
}

/*이미지 슬라이더*/
.carousel {
  background-color: black;
  width: 100%;
  height: 600px;
}

.carousel-item {
  width: 100%;
  height: 600px;
}

.carousel-item>img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.carousel-control-next-icon {
  width: 60px;
  height: 60px;
}

.carousel-control-prev-icon {
  width: 60px;
  height: 60px;
}

.product_slide {
  width: 1500px;
  height: 600px;
  margin: 40px auto;
}

.product_slide>h2 {
  text-align: center;
  margin: 60px 0 30px 0;
}

.slick-slider.slick-initialized {
  width: 1200px;
  height: 470px;
  margin: 0 auto;
}

.slick-arrow.slick-next {
  float: right;
  width: 34px;
  height: 34px;
  margin: -80px 0 0 0;
}

.slick-arrow.slick-prev {
  float: left;
  margin: -80px 0 0 0;
}

.slick-arrow.slick-next::before {
  color: #6d6d6d;
  font-size: 50px;
  content: ">";
}

.slick-arrow.slick-prev::before {
  color: #6d6d6d;
  font-size: 50px;
  content: "<";
}

.slick-track {
  height: 100%;
  background-color: #fff;
}

.nav-link>.slide_product {
  margin: 0 15px;
}

.nav-link .img_box {
  max-width: 265px;
  height: 310px;
  overflow: hidden;
}

.nav-link .img_box>img {
  transform: scale(1);
  transition: 0.8s;
  height: auto;
  width: 100%;
  cursor: pointer;
}

.nav-link .img_box>img:hover {
  transform: scale(1.1);
  transition: 0.6s;
}

.nav-link>.slide_product>h2 {
  font-size: 15px;
  color: #535353;
  font-weight: bold;
  margin: 10px 0 0 0;
}

.nav-link>.slide_product>h3 {
  font-size: 15px;
  color: #222222;
  margin: 5px 0;
  line-height: 1.4;
}

.nav-link>.slide_product>p {
  font-size: 14px;
  color: #222222;
  font-weight: bold;
}

